import React, { useState } from "react";
import queryString from "query-string";
import { useEffect } from "react";
import { useSelector } from "react-redux";
import { useDispatch } from "react-redux";
import { danhSachPhimPhanTrang, xoaPhimAdminAction} from "../../../redux/action/FilmAdminAction";
import PaginationPhim from "../TablePaginationPhimAdmin/PaginationPhim";

export default function TablePhanTrangPhim(props) {
  const dispatch = useDispatch();
  const danhSachPhimPhanTrangAdmin = useSelector(
    (state) => state.PhimReducer.danhSachPhimPhanTrang.items
  );
  const [filters, setFilters] = useState({
    maNhom: "GP01",
    soTrang: 1,
    soPhanTuTrenTrang: 10,
    maPhim: "",
  });

  const paramString = queryString.stringify(filters);
  useEffect(() => {
    const action = danhSachPhimPhanTrang(paramString);
    dispatch(action);
  }, [filters]);

  function handlePageChange(newPage) {
    setFilters({
      ...filters,
      soTrang: newPage,
    });
  }
  const handleXoaPhim = (maPhim) => {
    let action = xoaPhimAdminAction(maPhim);
    dispatch(action);
  };

  const renderDanhSachPhim = () => {
    return danhSachPhimPhanTrangAdmin.map((Phim, index) => {
      return (
        <tr key={index}>
          <td>{Phim.maPhim}</td>
          <td className="td_NameFilm">{Phim.tenPhim}</td>
          <td>
            <img
              style={{ width: "70px", height: "70px", objectFit: "cover" }}
              src={Phim.hinhAnh}
              alt="hinhAnh"
            />
          </td>
          <td>
            {Phim.moTa.length > 100
              ? Phim.moTa.substr(0, 50) + "..."
              : Phim.moTa}
          </td>
          <td>{Phim.maNhom}</td>
          <td style={{ width: "200px" }} >{Phim.ngayKhoiChieu.substr(0, 10)}</td>
          <td>
            <button
              className="btnCapNhat"
              onClick={() => {
                handleXoaPhim(Phim.maPhim);
              }}
            >
              Xa
            </button>
          </td>
        </tr>
      );
    });
  };
  return (
    <>
      <table className="table">
        <thead className="thead">
          <tr>
            <th>M Phim</th>
            <th>Tn Phim</th>
            <th>Hnh nh </th>
            <th>M T</th>
            <th>M Nhm</th>
            <th>Ngy Khi Chiu</th>
            <th>Cc Chc Nng</th>
          </tr>
        </thead>
        <tbody className="tbody">{renderDanhSachPhim()}</tbody>
      </table>
      <PaginationPhim onePageChangePhim={handlePageChange} />
    </>
  );
}
