import React from "react";
import { Row, Col } from "antd";
import { ErrorMessage, Field, Formik, Form } from 'formik';
import * as Yup from "yup";
import { useDispatch } from "react-redux";
import { themNguoiDungAction } from "../../../redux/action/UserAdminAction";
export default function ThemNguoiDung(props) {
  const dispatch = useDispatch();
  const {closerModal}= props;
  const validate =Yup.object().shape({
    taiKhoan: Yup.string()
      .required("*Ti khon khng c b trng!")
      .min(6, "*Ti Khon ti thiu 6 k t"),
    matKhau: Yup.string()
      .required("*Mt khu khng c b trng!")
      .max(32, "*Mt khu ti a 32 k t!")
      .min(5, "*Mt khu ti thiu 5 k t!"),

    email: Yup.string()
      .required("*Email khng c b trng!")
      .email("*Email khng ng nh dng!"),
    soDt: Yup.string()
      .matches(/^[0-9]+$/, "*S in thoi khng ng nh dng")
      .required("*S in thoi khng c b trng!")
      .min(10, "*S in thoi khng ng nh dng"),
    hoTen: Yup.string()
      .required("*H Tn khng c b trng!")
      .min(6, "*H Tn ti thiu 6 k t"),
  });
  return (
    <div>
        <Formik
            initialValues={{ 
            taiKhoan: "",
            matKhau: "",
            email: "",
            soDt: "",
            maNhom: "GP01",
            maLoaiNguoiDung: "KhachHang",
            hoTen: ""
            }}
            validationSchema={validate}
            onSubmit={(values,{resetForm})=>{
              const action = themNguoiDungAction(values);
              dispatch(action);
              resetForm({values:""});  
              closerModal(false)
            }}
            render={(formikProps)=>(
            <Form className="themNguoiDung" >
            <Row className="rowInput">
              <Col span={12}>
                <h2>Ti Khon</h2>
                <div>
                <Field
                    className="inputPhim"
                    type="text"
                    placeholder="Ti Khon"
                    value={formikProps.values.taiKhoan}
                    name="taiKhoan"
                    onChange={formikProps.handleChange}
                  />
                </div>
                <ErrorMessage name="taiKhoan">{msg => <div style={{color:"red"}}>{msg}</div>}</ErrorMessage>
              </Col>
              <Col span={12}>
                <h2>Email </h2>
                <div>
                <Field
                    className="inputPhim"
                    type="text"
                    placeholder="Email"
                    value={formikProps.values.email}
                    name="email"
                    onChange={formikProps.handleChange}
                  />
                </div>
                <ErrorMessage name="email">{msg => <div style={{color:"red"}}>{msg}</div>}</ErrorMessage>
              </Col>
            </Row>
            <Row className="rowInput">
              <Col span={12}>
                <h2>Mt Khu</h2>
                <div>
                <Field
                    className="inputPhim"
                    type="password"
                    placeholder="Mt Khu"
                    value={formikProps.values.matKhau}
                    name="matKhau"
                    onChange={formikProps.handleChange}
                  />
                </div>
                <ErrorMessage name="matKhau">{msg => <div style={{color:"red"}}>{msg}</div>}</ErrorMessage>
              </Col>
              <Col span={12}>
                <h2>S in Thoi</h2>
                <div>
                <Field
                    className="inputPhim"
                    type="text"
                    placeholder="S in thoi"
                    value={formikProps.values.soDt}
                    name="soDt"
                    onChange={formikProps.handleChange}
                  />
                </div>
                <ErrorMessage name="soDt">{msg => <div style={{color:"red"}}>{msg}</div>}</ErrorMessage>
              </Col>
            </Row>
            <Row className="rowInput">
              <Col span={12}>
                <h2>H Tn Ngi Dng</h2>
                <div>
                <Field
                    className="inputPhim"
                    type="text"
                    placeholder="H Tn"
                    value={formikProps.values.hoTen}
                    name="hoTen"
                    onChange={formikProps.handleChange}
                  />
                </div>
                <ErrorMessage name="hoTen">{msg => <div style={{color:"red"}}>{msg}</div>}</ErrorMessage>
              </Col>
              <Col span={12}>
                <h2>Loi Ngi Dng</h2>
                <div>
                  <Field
                    component="select"
                    className="selectNguoiDung"
                    onChange={formikProps.handleChange}
                    name="maLoaiNguoiDung"
                  >
                    <option>KhachHang</option>
                    <option>QuanTri</option>
                  </Field>
                </div>
              </Col>
            </Row>
            <div className="div_btnThemNguoiDung">
              <button type="submit"   className="btnThemNguoiDung">Thm</button>
            </div>
          </Form>
        )} />
    </div>
  );
}
