import React from "react";
import { useDispatch } from "react-redux";
import logoDangNhap from "../../assets/img/logoDangNhap.png";
import { NavLink } from "react-router-dom";

import { ErrorMessage, Field, Formik, Form } from 'formik';

import * as Yup from "yup";
import { dangNhap } from "../../redux/action/NguoiDungAction";
export default function DangNhap(props) {
  const dispatch = useDispatch();
  const validate=Yup.object().shape({
    taiKhoan: Yup.string().required("*Ti khon khng c b trng!"),
    matKhau: Yup.string().required("*Mt khu khng c b trng!")
  })
 
  const handleClose = () => {
    props.history.push("/");
  };
  return (
    <Formik
    initialValues={{ 
      taiKhoan: "",
      matKhau: "",
    }}
    validationSchema={validate}
    onSubmit={(values)=>{
      const action = dangNhap(values);
      dispatch(action); 
    }}
    render={(formikProps)=>(
    <Form>
      <div className="dangNhap__Img">
        <div className="dangNhap__Khung">
          <img
            className="dangNhap__Logo"
            src={logoDangNhap}
            alt="logoDangNhap"
          />
          <div className="dangNhap__noiDung">
            ng nhp  c nhiu u i, mua v
            <br />
            v bo mt thng tin!
          </div>
          <div className="dangNhap__Input">
           <Field
              className="input"
              type="text"
              placeholder="Ti Khon"
              name="taiKhoan"
              onChange={formikProps.handleChange}
              onBlur={formikProps.handleBlur}
            />
            <ErrorMessage name="taiKhoan">{msg => <div className="canhBao">{msg}</div>}</ErrorMessage>

          </div>
          <div className="dangNhap__Input">
           <Field
              className="input"
              type="password"
              placeholder="Mt Khu"
              name="matKhau"
              onChange={formikProps.handleChange}
              onBlur={formikProps.handleBlur}
            />
            <ErrorMessage name="matKhau">{msg => <div className="canhBao">{msg}</div>}</ErrorMessage>

          </div>
          <button className="btn__DangNhap">ng Nhp</button>
          <div>
            <span className="spanTaiKhoan">Bn cha c ti khon ?</span>
            <NavLink to="dangKy" className="dangKy">ng K</NavLink>
          </div>
          <div className="dangNhap__Dong">
            <button type="button" className="btn__Dong" onClick={handleClose}>
              X
            </button>
          </div>
        </div>
      </div>
    </Form>
    )}/>
  );
}
