import React, {useEffect,useState}from 'react';
import { USER_LOGIN } from '../../../redux/constants/NguoiDungConst';
import Swal from "sweetalert2";
import {quanLyAdminServices} from "../../../redux/service/QuanLyAdminServices";
import {quanLyNguoiDungServices } from "../../../redux/service/QuanLyNguoiDungServices";
import { useDispatch } from "react-redux";
import { showLoading } from '../../../redux/action/NguoiDungAction';
import Loading from '../../../ultil/Loading/Loading';

export default function UserUpdate(props) {
   const dispatch = useDispatch();
   const [loading, setLoading]=useState(true)
    const info = JSON.parse(localStorage.getItem(USER_LOGIN));
 
    let [account, setAccount] = useState({
    taiKhoan: info.taiKhoan,
  });
  let [state, setState] = useState({
    values: {
      hoTen: "",
      taiKhoan: "",
      email: "",
      soDT: "",
      maLoaiNguoiDung: "KhachHang",
      maNhom: "GP01",
      matKhau: "",
    },
    errors: {
      hoTen: "",
      taiKhoan: "",
      matKhau: "",
      email: "",
      soDT: "",
    },
  });
  useEffect(() => {
    dispatch(showLoading())
    quanLyNguoiDungServices
      .layThongTinTaiKhoan(account)
      .then((res) => {
        setState({
          values: res.data,
          errors: {
            hoTen: "",
            taiKhoan: "",
            matKhau: "",
            email: "",
            soDT: "",
          },
        });
        setLoading(false)
      })
      .catch((err) => {
        console.log(err.response.data);
      });
  }, [account]);

  const handleChangeInput = (event) => {
    var { value, name } = event.target;
    let newValues = {
      ...state.values,
      [name]: value,
    };
    let newErrors = {
      ...state.errors,
      [name]: value === "" ? "Khng c b trng!" : "",
    };

    if (name === "email") {
      let regexEmail =
        "^[a-z][a-z0-9_.]{5,32}@[a-z0-9]{2,}(.[a-z0-9]{2,4}){1,2}$";
      if (value.match(regexEmail)) {
        newErrors.email = "";
      } else {
        newErrors.email = "Email khng hp l";
      }
    }
    setState({ values: newValues, errors: newErrors });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    let valid = true;
    let { values, errors } = state;
    for (let key in values) {
      if (values[key] === "") {
        // kim tra li
        valid = false;
      }
    }
    for (let key in errors) {
      if (errors[key] !== "") {
        valid = false;
      }
    }
    if (!valid) {
        Swal.fire({
            icon: 'error',
            title: 'Oops...',
            text: "Thng tin khng hp l",
            });
      return;
    }
    let infoUserUpdate = {
        taiKhoan: values.taiKhoan,
        matKhau: values.matKhau,
        hoTen: values.hoTen,
        email: values.email,
        soDT:values.soDT,
        maNhom: values.maNhom,
        maLoaiNguoiDung: info.maLoaiNguoiDung,
    };
    console.log(infoUserUpdate)
    quanLyAdminServices
      .capNhatThongTinNguoiDung(infoUserUpdate)
      .then((res) => {
       
        Swal.fire({
            position: 'top-center',
            icon: 'success',
            title: 'Cp nht ngi dng thnh cng',
            showConfirmButton: false,
            timer: 2000
          });
        })
        .catch((err) => {
            console.log(err)
        Swal.fire({
            icon: 'error',
            title: 'Oops...',
            text: "Nhp li thng tin",
            });
        });
    };
    return (
         <form onSubmit={handleSubmit}>
            <div className="userUpdate">
                <div className="taiKhoan">
                <p className="lable"> Ti Khon</p>
                <input
                    style={{ backgroundColor: "darkgray" }}
                    className="input"
                    name="taiKhoan"
                    type="text"
                    placeholder="Tn ti khon"
                    value={state.values.taiKhoan}
                    onChange={handleChangeInput}
                    disabled
                 />
               <p className="canhBaoTai">{state.errors.taiKhoan}</p>
                </div>
                <div className="hoTen">
                <p className="lable">H Tn</p>
                <input
                    className="input"
                    name="hoTen"
                    type="text"
                    placeholder="H Tn"
                    value={state.values.hoTen}
                    onChange={handleChangeInput}
                 />
               <p className="canhBaoCap">{state.errors.hoTen}</p>
                
                </div>
                <div className="email">
                <p className="lable">Email</p>
                <input
                    className="input"
                    name="email"
                    placeholder="Email"
                    value={state.values.email}
                    onChange={handleChangeInput}
                />
                <p className="canhBaoCap">{state.errors.email}</p>
                </div>
                <div className="soDt">
                <p className="lable">S in Thoi</p>
                <input
                    className="input"
                    name="soDT"
                    type="text"
                    placeholder="S in thoi"
                    value={state.values.soDT}
                    onChange={handleChangeInput}
                />
                <p className="canhBaoCap">{state.errors.soDT}</p>
                </div>
                <div className="matKhau">
                <p className="lable">Mt Khu</p>
                <input
                    className="input"
                    name="matKhau"
                    type="password"
                    placeholder="Mt khu"
                    value={state.values.matKhau}
                    onChange={handleChangeInput}
                />
                <p className="canhBaoCap">{state.errors.matKhau}</p>   
                </div>
                <div className="divBtn">
                <button type="submit" className="btnCapNhat">
                    Cp Nht
                </button>
                </div>
            </div>
             {loading?<Loading/>:""} 
    </form>
    )
}
