import React from "react";
import { useDispatch } from "react-redux";
import logoDangKy from "../../assets/img/logoDangNhap.png";
import { NavLink } from "react-router-dom";
import { ErrorMessage, Field, Formik, Form } from 'formik';
import * as Yup from "yup";
import { dangKy } from "../../redux/action/NguoiDungAction";

export default function DangKy(props) {
  const dispatch = useDispatch();
  const validate =Yup.object().shape({
    taiKhoan: Yup.string()
      .required("*Ti khon khng c b trng!")
      .min(6, "*Ti Khon ti thiu 6 k t"),
    matKhau: Yup.string()
      .required("*Mt khu khng c b trng!")
      .max(32, "*Mt khu ti a 32 k t!")
      .min(5, "*Mt khu ti thiu 5 k t!"),

    email: Yup.string()
      .required("*Email khng c b trng!")
      .email("*Email khng ng nh dng!"),
    soDt: Yup.string()
      .matches(/^[0-9]+$/, "*S in thoi khng ng nh dng")
      .required("*S in thoi khng c b trng!")
      .min(10, "*S in thoi khng ng nh dng"),
    hoTen: Yup.string()
      .required("*H Tn khng c b trng!")
      .min(6, "*H Tn ti thiu 6 k t"),
  });
  const handleClose = () => {
    props.history.push("/");
  };
  return (
    <Formik
    initialValues={{ 
      taiKhoan: "",
      matKhau: "",
      email: "",
      soDt: "",
      maNhom: "GP01",
      maLoaiNguoiDung: "KhachHang",
      hoTen: ""
    }}
    validationSchema={validate}
    onSubmit={(values)=>{
      const action = dangKy(values);
      dispatch(action);  
    }}
    render={(formikProps)=>(
    <Form >
      <div className="dangKy__Img">
        <div className="dangKy__Khung">
          <img className="dangKy__Logo" src={logoDangKy} alt="logodangKy" />

          <div className="dangKy__Input">
            <p className="ten__Input">Ti Khon</p>
           <Field
              className="input"
              type="text"
              placeholder="Ti Khon"
              name="taiKhoan"
              onChange={formikProps.handleChange}
              onBlur={formikProps.handleBlur}
            />
            <ErrorMessage name="taiKhoan">{msg => <div style={{color:"red"}}>{msg}</div>}</ErrorMessage>

          </div>
          <div className="dangKy__Input">
            <p className="ten__Input">Mt Khu</p>
           <Field
              className="input"
              type="password"
              placeholder="Mt Khu"
              name="matKhau"
              onChange={formikProps.handleChange}
              onBlur={formikProps.handleBlur}
            />
            <ErrorMessage name="matKhau">{msg => <div style={{color:"red"}}>{msg}</div>}</ErrorMessage>
          </div>

          <div className="dangKy__Input">
            <p className="ten__Input">H Tn</p>
           <Field
              className="input"
              type="text"
              placeholder="H Tn"
              name="hoTen"
              onChange={formikProps.handleChange}
              onBlur={formikProps.handleBlur}
            />
            <ErrorMessage name="hoTen">{msg => <div style={{color:"red"}}>{msg}</div>}</ErrorMessage>
          </div>
          <div className="dangKy__Input">
            <p className="ten__Input">Email</p>
           <Field
              className="input"
              type="text"
              placeholder="Email"
              name="email"
              onChange={formikProps.handleChange}
              onBlur={formikProps.handleBlur}
            />
            <ErrorMessage name="email">{msg => <div style={{color:"red"}}>{msg}</div>}</ErrorMessage>
          </div>
          <div className="dangKy__Input">
            <p className="ten__Input">S in Thoi</p>
           <Field
              className="input"
              type="text"
              placeholder="S in thoi"
              name="soDt"
              onChange={formikProps.handleChange}
              onBlur={formikProps.handleBlur}
            />
            <ErrorMessage name="soDt">{msg => <div style={{color:"red"}}>{msg}</div>}</ErrorMessage>
          </div>
          <button className="btn__dangKy">ng K</button>
          <div className="taiKhoan_Link">
            <span className="spanTaiKhoan">Bn  c ti khon!</span>
            <NavLink to="/dangNhap" className="dangNhap">ng Nhp</NavLink>
          </div>
          <div className="dangKy__Dong">
            <button type="button" className="btn__Dong" onClick={handleClose}>
              X
            </button>
          </div>
        </div>
      </div>
    </Form>
    )}/>
  );
}
